import React, { useState, useRef, useEffect, useCallback } from 'react';
import { RightOutlined, ArrowUpOutlined } from '@ant-design/icons';
import { Tag } from 'antd';
import { marked } from 'marked';
import logo from './img/logo.svg';
import './Chat.scss';
import aiResponses from './aiResponses';
import { IconSparkles, IconAt } from '@tabler/icons-react';
import ContentEditable from 'react-contenteditable';
import { useFloating, autoUpdate, offset, flip, shift } from '@floating-ui/react';

const UserMessage = ({ text }) => (
  <div className="user-message">
    <span>{text}</span>
  </div>
);

const AiMessage = ({ text, typewriter, onTypewriterEnd }) => {
  const [displayed, setDisplayed] = useState(typewriter ? '' : text);
  useEffect(() => {
    if (typewriter) {
      setDisplayed('');
      let i = 0;
      const interval = setInterval(() => {
        setDisplayed(text.slice(0, i + 1));
        i++;
        if (i === text.length) {
          clearInterval(interval);
          if (onTypewriterEnd) onTypewriterEnd();
        }
      }, 25);
      return () => clearInterval(interval);
    }
  }, [text, typewriter, onTypewriterEnd]);

  return (
    <div className="ai-message">
      <span className="chat-ai-icon"><IconSparkles size={20} stroke={2} /></span>
      <p
        className="ai-message-markdown"
        dangerouslySetInnerHTML={{ __html: marked.parse(typewriter ? displayed : text) }}
      />
      {typewriter && displayed.length < text.length && (
        <span className="ai-typing-dots">
          <span>.</span><span>.</span><span>.</span>
        </span>
      )}
    </div>
  );
};

const getRandomAiResponse = () => {
  const idx = Math.floor(Math.random() * aiResponses.length);
  return aiResponses[idx];
};

const initialAiGreeting = { sender: 'ai', text: 'Olá! Como posso ajudar você hoje?' };

const highlightTags = (text) => {
  // Preserva quebras de linha
  return text
    .split(/\n|<br\s*\/?\s*>/)
    .map(line =>
      line.replace(/@([\w\- ]+)(\s*)/g, (match, tagText, space) => {
        const found = TAG_SUGGESTIONS.some(s => s.toLowerCase() === tagText.trim().toLowerCase());
        if (found) {
          if (space.length === 0) return `<span class=\"chat-tag\">@${tagText}</span>\u200B`;
          return `<span class=\"chat-tag\">@${tagText}</span>${'&nbsp;'.repeat(space.length)}`;
        } else {
          return `@${tagText}${space}`;
        }
      })
    )
    .join('<br>');
};

// Sugestões de exemplo (pode ser importado de outro arquivo se desejar)
const TAG_SUGGESTIONS = [
  'areas_quentes', 'base_1', 'base_2', 'lojas jun-2025', 'lojas mai-2025', 'lojas abr-2025'
];

// Placeholder animado com efeito typewriter
const PLACEHOLDER_MESSAGES = [
  'Digite aqui para criar uma camada ou para tirar uma dúvida',
  'Digite @ para inserir um contexto de uma base de dados'
];

const Chat = () => {
  const [messages, setMessages] = useState([initialAiGreeting]);
  const [inputHtml, setInputHtml] = useState('');
  const [tags, setTags] = useState([]);
  const [aiTyping, setAiTyping] = useState(false);
  const [pendingAiMsg, setPendingAiMsg] = useState('');
  const [showTypewriter, setShowTypewriter] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [suggestions, setSuggestions] = useState([]);
  const [selectedSuggestion, setSelectedSuggestion] = useState(0);
  const messagesEndRef = useRef(null);
  const contentEditableRef = useRef(null);
  const dropdownRef = useRef(null);
  const floatingRef = useRef(null);
  const [floatingPos, setFloatingPos] = useState({ x: 0, y: 0 });
  const { refs, floatingStyles, update } = useFloating({
    placement: 'top-start',
    middleware: [offset(0), flip(), shift()],
    whileElementsMounted: autoUpdate,
  });
  const [dropdownAnchorId, setDropdownAnchorId] = useState(null);
  const suggestionItemRefs = useRef([]);
  const selectedSuggestionRef = useRef(selectedSuggestion);
  const [placeholderIdx, setPlaceholderIdx] = useState(0);
  const [placeholderText, setPlaceholderText] = useState('');
  const [typing, setTyping] = useState(true); // true: digitando, false: apagando
  const [inputFocused, setInputFocused] = useState(false);

  useEffect(() => {
    selectedSuggestionRef.current = selectedSuggestion;
  }, [selectedSuggestion]);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages, aiTyping, showTypewriter]);

  // Permite abrir a lista ao clicar em uma tag dentro do ContentEditable
  useEffect(() => {
    const inputEl = contentEditableRef.current?.el?.current;
    if (!inputEl) return;
    function handleTagClick(e) {
      if (e.target.classList.contains('chat-tag')) {
        setSuggestions(TAG_SUGGESTIONS.filter(item => !tags.includes(item)));
        setShowSuggestions(true);
        setSelectedSuggestion(0);
        setDropdownAnchorId('dropdown-anchor-tag-' + e.target.innerText.replace('@', ''));
        setTimeout(() => {
          contentEditableRef.current?.el.current?.focus();
        }, 0);
      }
    }
    inputEl.addEventListener('click', handleTagClick);
    return () => inputEl.removeEventListener('click', handleTagClick);
  }, [tags]);

  // Atualiza o anchor para menção em digitação
  useEffect(() => {
    if (showSuggestions && suggestions.length > 0) {
      // Procura por menção em digitação
      const inputEl = contentEditableRef.current?.el?.current;
      if (inputEl) {
        const match = /(@\w*)$/.exec(inputEl.innerText);
        if (match) {
          setDropdownAnchorId('dropdown-anchor-mention');
        }
      }
    }
  }, [showSuggestions, suggestions]);

  // Após render, associa o ref ao anchor
  useEffect(() => {
    if (!dropdownAnchorId) return;
    const anchor = document.getElementById(dropdownAnchorId);
    if (anchor) {
      dropdownRef.current = anchor;
      refs.setReference(anchor);
      update && update();
    }
  }, [dropdownAnchorId, inputHtml, refs, update]);

  // Função para inserir o anchor no inputHtml, agora com estilo inline para garantir visibilidade
  const getInputHtmlWithAnchor = () => {
    let html = inputHtml;
    // Se for menção em digitação
    if (showSuggestions && suggestions.length > 0 && dropdownAnchorId === 'dropdown-anchor-mention') {
      html = html.replace(/(@\w*)\u200B?$/, '$1<span id="dropdown-anchor-mention" style="display:inline-block;width:1px;height:1em;vertical-align:middle;"></span>');
    }
    // Se for tag clicada
    tags.forEach(tag => {
      const tagHtml = `<span class=\"chat-tag\">@${tag}</span>\u200B`;
      const anchorHtml = `<span class=\"chat-tag\">@${tag}</span><span id=\"dropdown-anchor-tag-${tag}\" style=\"display:inline-block;width:1px;height:1em;vertical-align:middle;\"></span>\u200B`;
      if (dropdownAnchorId === 'dropdown-anchor-tag-' + tag) {
        html = html.replace(tagHtml, anchorHtml);
      }
    });
    return html;
  };

  const handleSend = (e) => {
    e.preventDefault();
    const textToSend = contentEditableRef.current.el.current.innerText.trim();

    if (!textToSend) return;

    setMessages([...messages, { sender: 'user', text: textToSend }]);

    setInputHtml('');
    setTags([]);
    setAiTyping(true);
    setShowTypewriter(false);
    const aiMsg = getRandomAiResponse();
    setPendingAiMsg(aiMsg);
    setTimeout(() => {
      setShowTypewriter(true);
    }, 2000);
  };

  const handleInputChange = (e) => {
    // Usa innerHTML para preservar <br> (quebra de linha)
    const rawHtml = contentEditableRef.current.el.current.innerHTML;
    // Remove �B
    const cleanHtml = rawHtml.replace(/\u200B/g, '');
    const newHtml = highlightTags(cleanHtml.replace(/<div>/g, '').replace(/<\/div>/g, '<br>'));
    setInputHtml(newHtml);

    // Detecta se está digitando uma tag
    // Só mostra sugestões se o evento NÃO for de deleção de tag
    if (e && e.nativeEvent && (e.nativeEvent.inputType === 'deleteContentBackward' || e.nativeEvent.inputType === 'deleteContentForward')) {
      setShowSuggestions(false);
      return;
    }
    const match = /(^|\s)@(\w*)$/.exec(cleanHtml);
    if (match) {
      const search = match[2].toLowerCase();
      const filtered = TAG_SUGGESTIONS.filter(item => item.toLowerCase().includes(search) && !tags.includes(item));
      setSuggestions(filtered);
      setShowSuggestions(true);
    } else {
      setShowSuggestions(false);
    }
  };

  const handleKeyDown = (e) => {
    if (showSuggestions && (e.key === 'ArrowDown' || e.key === 'ArrowUp')) {
      e.preventDefault();
      setSelectedSuggestion(prev => {
        let next;
        if (e.key === 'ArrowDown') next = (prev + 1) % suggestions.length;
        if (e.key === 'ArrowUp') next = (prev - 1 + suggestions.length) % suggestions.length;
        // Scroll para o item selecionado
        setTimeout(() => {
          const el = suggestionItemRefs.current[next];
          if (el) el.scrollIntoView({ block: 'nearest', behavior: 'smooth' });
        }, 0);
        return next;
      });
      return;
    }
    if (showSuggestions && e.key === 'Enter') {
      e.preventDefault();
      const idx = selectedSuggestionRef.current;
      if (suggestions.length > 0 && idx >= 0 && idx < suggestions.length) {
        insertTagFromSuggestion(suggestions[idx]);
      } else if (suggestions.length === 0) {
        // Remove o @palavra do final do texto
        const rawText = contentEditableRef.current.el.current.innerText.replace(/\u200B/g, '');
        const newText = rawText.replace(/@[^\s@]*\s*$/, '');
        setInputHtml(newText);
        setShowSuggestions(false);
        setTimeout(() => {
          const el = contentEditableRef.current.el.current;
          if (el) {
            const range = document.createRange();
            const sel = window.getSelection();
            range.selectNodeContents(el);
            range.collapse(false);
            sel.removeAllRanges();
            sel.addRange(range);
          }
        }, 0);
      }
      return;
    }
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend(e);
      return;
    }

    // --- NOVA LÓGICA PARA DELETAR TAG INTEIRA ---
    if (e.key === 'Backspace' || e.key === 'Delete') {
      const sel = window.getSelection();
      if (sel && sel.rangeCount > 0) {
        const range = sel.getRangeAt(0);
        let container = range.startContainer;
        let offset = range.startOffset;
        // Se o container for um nó de texto dentro do span.chat-tag
        if (container.nodeType === 3 && container.parentNode.classList && container.parentNode.classList.contains('chat-tag')) {
          // Seleciona o span inteiro
          const tagSpan = container.parentNode;
          e.preventDefault();
          // Remove o span do DOM
          tagSpan.remove();
          // Atualiza o inputHtml
          const newHtml = contentEditableRef.current.el.current.innerHTML.replace(/<span class=\"chat-tag\">@?\w+<\/span>\u200B?/g, '');
          setInputHtml(newHtml);
          // Reposiciona o cursor
          setTimeout(() => {
            contentEditableRef.current.el.current.focus();
          }, 0);
          return;
        }
        // Se o cursor está logo após um span.chat-tag (Backspace)
        if (e.key === 'Backspace' && container.nodeType === 3 && offset === 0 && container.previousSibling && container.previousSibling.classList && container.previousSibling.classList.contains('chat-tag')) {
          e.preventDefault();
          const tagSpan = container.previousSibling;
          tagSpan.remove();
          const newHtml = contentEditableRef.current.el.current.innerHTML.replace(/<span class=\"chat-tag\">@?\w+<\/span>\u200B?/g, '');
          setInputHtml(newHtml);
          setTimeout(() => {
            contentEditableRef.current.el.current.focus();
          }, 0);
          return;
        }
        // Se o cursor está logo antes de um span.chat-tag (Delete)
        if (e.key === 'Delete' && container.nodeType === 3 && offset === container.length && container.nextSibling && container.nextSibling.classList && container.nextSibling.classList.contains('chat-tag')) {
          e.preventDefault();
          const tagSpan = container.nextSibling;
          tagSpan.remove();
          const newHtml = contentEditableRef.current.el.current.innerHTML.replace(/<span class=\"chat-tag\">@?\w+<\/span>\u200B?/g, '');
          setInputHtml(newHtml);
          setTimeout(() => {
            contentEditableRef.current.el.current.focus();
          }, 0);
          return;
        }
      }
    }
  };

  function insertTagFromSuggestion(tag) {
    // Opera sobre o texto puro, substituindo o último @palavra (ou só @) por @tag  , depois aplica highlightTags
    const rawText = contentEditableRef.current.el.current.innerText.replace(/\u200B/g, '');
    // Substitui o último @palavra (ou só @) por @tag  (dois espaços)
    const newText = rawText.replace(/@[^\s@]*\s*$/, `@${tag}  `);
    setInputHtml(highlightTags(newText));
    setShowSuggestions(false);
    // Garante que o cursor fique após o espaço
    setTimeout(() => {
      const el = contentEditableRef.current.el.current;
      if (el) {
        // Procura o último span.chat-tag
        const tags = el.querySelectorAll('span.chat-tag');
        if (tags.length > 0) {
          const lastTag = tags[tags.length - 1];
          // Procura o próximo nó após o span (deve ser um &nbsp; ou text node)
          let node = lastTag.nextSibling;
          // Se for um entity &nbsp; (NodeType 3, text), coloca o cursor depois
          const range = document.createRange();
          const sel = window.getSelection();
          if (node && node.nodeType === 3) {
            range.setStart(node, node.length);
            range.collapse(true);
          } else {
            // fallback: coloca após o span
            range.setStartAfter(lastTag);
            range.collapse(true);
          }
          sel.removeAllRanges();
          sel.addRange(range);
        }
      }
    }, 0);
  }

  const handleTagRemove = (removedTag) => {
    setTags(tags.filter(tag => tag !== removedTag));
  };

  const handleTypewriterEnd = () => {
    setMessages((msgs) => [
      ...msgs,
      { sender: 'ai', text: pendingAiMsg }
    ]);
    setAiTyping(false);
    setShowTypewriter(false);
    setPendingAiMsg('');
  };

  const handleLogoClick = () => {
    setMessages([initialAiGreeting]);
    setInputHtml('');
    setTags([]);
    setAiTyping(false);
    setPendingAiMsg('');
    setShowTypewriter(false);
  };

  // Placeholder animado com efeito typewriter
  useEffect(() => {
    let timeout;
    const fullText = PLACEHOLDER_MESSAGES[placeholderIdx];
    if (typing) {
      if (placeholderText.length < fullText.length) {
        timeout = setTimeout(() => {
          setPlaceholderText(fullText.slice(0, placeholderText.length + 1));
        }, 80);
      } else {
        // Espera antes de começar a apagar
        timeout = setTimeout(() => setTyping(false), 8000);
      }
    } else {
      if (placeholderText.length > 0) {
        timeout = setTimeout(() => {
          setPlaceholderText(fullText.slice(0, placeholderText.length - 1));
        }, 20);
      } else {
        // Troca para a próxima frase e começa a digitar
        timeout = setTimeout(() => {
          setPlaceholderIdx(idx => (idx + 1) % PLACEHOLDER_MESSAGES.length);
          setTyping(true);
        }, 300);
      }
    }
    return () => clearTimeout(timeout);
  }, [placeholderText, typing, placeholderIdx]);

  // Quando muda a frase, zera o texto
  useEffect(() => {
    setPlaceholderText('');
  }, [placeholderIdx]);

  // Função para abrir sugestões de tags ao clicar no botão
  const handleAddContextClick = (e) => {
    e.preventDefault();
    // Se não houver @ em aberto, insere um @ no final do texto
    const el = contentEditableRef.current?.el?.current;
    if (el) {
      let text = el.innerText.replace(/\u200B/g, '');
      // Só insere @ se não houver @palavra em aberto no final
      if (!/@[^\s@]*$/.test(text)) {
        text = text.replace(/\s+$/, '') + ' @';
        setInputHtml(highlightTags(text));
        setTimeout(() => {
          // Move o cursor para o final
          const el2 = contentEditableRef.current?.el?.current;
          if (el2) {
            const range = document.createRange();
            const sel = window.getSelection();
            range.selectNodeContents(el2);
            range.collapse(false);
            sel.removeAllRanges();
            sel.addRange(range);
          }
        }, 0);
      }
    }
    setSuggestions(TAG_SUGGESTIONS.filter(item => !tags.includes(item)));
    setShowSuggestions(true);
    setSelectedSuggestion(0);
    setTimeout(() => {
      contentEditableRef.current?.el.current?.focus();
    }, 0);
  };

  return (
    <div className="chat-onmaps">
      <header className="chat-header">
        <img src={logo} alt="logo" style={{ cursor: 'pointer' }} onClick={handleLogoClick} />
        <span className="chat-chevron"><RightOutlined /></span>
      </header>
      <div className="message-log">
        {messages.map((msg, idx) =>
          msg.sender === 'user' ? (
            <UserMessage key={idx} text={msg.text} />
          ) : (
            <AiMessage key={idx} text={msg.text} typewriter={false} />
          )
        )}
        {aiTyping && !showTypewriter && (
          <div className="ai-message">
            <span className="chat-ai-icon"><IconSparkles size={20} stroke={2} /></span>
            <span className="ai-typing-dots"><span>.</span><span>.</span><span>.</span></span>
          </div>
        )}
        {aiTyping && showTypewriter && (
          <AiMessage text={pendingAiMsg} typewriter={true} onTypewriterEnd={handleTypewriterEnd} />
        )}
        <div ref={messagesEndRef} />
      </div>
      <form className="chat-footer" onSubmit={handleSend}>
        <div className="chat-input-wrapper" onClick={() => contentEditableRef.current?.el.current?.focus()}>
          
          {tags.map((tag) => (
            <Tag
              key={tag}
              closable
              onClose={() => handleTagRemove(tag)}
              onClick={e => {
                e.stopPropagation();
                setSuggestions(TAG_SUGGESTIONS.filter(item => !tags.includes(item)));
                setShowSuggestions(true);
                setSelectedSuggestion(0);
                setTimeout(() => {
                  contentEditableRef.current?.el.current?.focus();
                }, 0);
              }}
            >
              @{tag}
            </Tag>
          ))}
          <ContentEditable
            ref={node => {
              contentEditableRef.current = node;
              refs.setReference(node?.el?.current);
            }}
            className="chat-input"
            html={getInputHtmlWithAnchor()}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
            onFocus={() => setInputFocused(true)}
            onBlur={() => setInputFocused(false)}
            tagName="div"
          />
          {/* Placeholder animado */}
          {(!inputHtml || inputHtml === '<br>') && !inputFocused && (
            <span className="chat-input-placeholder">{placeholderText}</span>
          )}
          {showSuggestions && (
            <div
              ref={floatingRef}
              className="chat-suggestions-dropdown"
              style={{
                ...floatingStyles,
                position: floatingStyles.position || 'absolute',
                width: contentEditableRef.current?.el?.current?.offsetWidth || 320
              }}
            >
              <ul>
                {suggestions.length === 0 ? (
                  <li className="no-suggestion">Resultado não encontrado</li>
                ) : (
                  suggestions.map((s, i) => (
                    <li
                      key={s}
                      ref={el => suggestionItemRefs.current[i] = el}
                      className={i === selectedSuggestion ? 'selected' : ''}
                      onMouseDown={e => {
                        e.preventDefault();
                        insertTagFromSuggestion(s);
                        setShowSuggestions(false);
                      }}
                    >
                      {s}
                    </li>
                  ))
                )}
              </ul>
            </div>
          )}
          <button className="chat-send-btn" type="submit">
            <ArrowUpOutlined />
          </button>
          <button className="chat-add-context" type="button" onClick={handleAddContextClick}>
            <IconAt/>Inserir base
          </button>
        </div>
      </form>
    </div>
  );
};

export default Chat; 